interface NodeRecordProps {
    id: NodeId;
    name: NodeName;
    description: NodeDescription;
    type: NodeType;
    link: NodeLink | null;
    tags: List<NodeTag>;
    layer: boolean;
    defaultLayer: boolean;
    parent: Node | null;
    childrenList: List<Node>;
    sourcesList: List<Node>;
    targetsList: List<Node>;
    geometry: GraphPoint;
    size: Size;
    metadata: string | null;
    parsedMetadata?: {[metaKey: string]: any};
    enabled: boolean;
    version?: number;
}

interface RawNodeRecordProps {
    id: NodeId;
    name: NodeName;
    description: NodeDescription;
    type: NodeType;
    link: NodeLink | null;
    tags: string[];
    layer: boolean;
    defaultLayer: boolean;
    parent: Node | null;
    childrenList: Node[];
    sourcesList: Node[];
    targetsList: Node[];
    geometry: {x: number, y: number};
    size: {width: number, height: number};
    metadata: string | null;
    enabled: boolean;
}

const defaultNodeRecordProps: NodeRecordProps = {
    id: '',
    name: '',
    description: '',
    type: NodeType.misc,
    link: null,
    tags: List(),
    layer: false,
    defaultLayer: false,
    parent: null,
    childrenList: List(),
    sourcesList: List(),
    targetsList: List(),
    geometry: {x:0, y:0},
    size: {width: 0, height: 0},
    metadata: null,
    enabled: true,
    version: 1,
};

export const makeNodeRecord = (node: Node, withoutParsedMetadata?: boolean): NodeRecord => {
    return new NodeRecord({
        id: node.getId(),
        name: node.getName(),
        description: node.getDescription(),
        type: node.getType(),
        link: node.getLink(),
        tags: node.getTags(),
        layer: node.isLayer(),
        defaultLayer: node.isDefaultLayer(),
        parent: node.getParent()? makeParentNodeRecord(node.getParent() as Node, node, withoutParsedMetadata) : null,
        childrenList: node.children().map(child => makeNodeRecord(child, withoutParsedMetadata)),
        sourcesList: node.getSources().map(s => makeConnectedNodeRecord(s, withoutParsedMetadata)),
        targetsList: node.getTargets().map(t => makeConnectedNodeRecord(t, withoutParsedMetadata)),
        geometry: new GraphPointRecord(node.getGeometry()),
        size: new SizeRecord(node.getSize()),
        metadata: node.getMetadata(),
        parsedMetadata: withoutParsedMetadata ? undefined : node.getParsedMetadata(),
        enabled: node.isEnabled(),
    });
};

const makeConnectedNodeRecord = (node: Node, withoutParsedMetadata?: boolean): NodeRecord => {
    return new NodeRecord({
        id: node.getId(),
        name: node.getName(),
        description: node.getDescription(),
        type: node.getType(),
        link: node.getLink(),
        tags: node.getTags(),
        layer: node.isLayer(),
        defaultLayer: node.isDefaultLayer(),
        parent: node.getParent()? makeParentNodeRecord(node.getParent() as Node, node, withoutParsedMetadata) : null,
        childrenList: node.children().map(c => makeNodeRecord(c, withoutParsedMetadata)),
        sourcesList: List(),
        targetsList: List(),
        geometry: new GraphPointRecord(node.getGeometry()),
        size: new SizeRecord(node.getSize()),
        metadata: node.getMetadata(),
        parsedMetadata: withoutParsedMetadata ? undefined : node.getParsedMetadata(),
        enabled: node.isEnabled(),
    });
};

const makeParentNodeRecord = (parent: Node, referencingChild: Node, withoutParsedMetadata?: boolean): NodeRecord => {
    return new NodeRecord({
        id: parent.getId(),
        name: parent.getName(),
        description: parent.getDescription(),
        type: parent.getType(),
        link: parent.getLink(),
        tags: parent.getTags(),
        layer: parent.isLayer(),
        defaultLayer: parent.isDefaultLayer(),
        parent: parent.getParent()? makeParentNodeRecord(parent.getParent() as Node, parent, withoutParsedMetadata) : null,
        childrenList: List(),
        sourcesList: List(),
        targetsList: List(),
        geometry: new GraphPointRecord(parent.getGeometry()),
        size: new SizeRecord(parent.getSize()),
        metadata: parent.getMetadata(),
        parsedMetadata: withoutParsedMetadata ? undefined : parent.getParsedMetadata(),
        enabled: parent.isEnabled()
    });
}

// tslint:disable-next-line:max-classes-per-file
class NodeRecord extends Record(defaultNodeRecordProps) implements Node {

    private parsedMetadataCache: {[metaKey: string]: any} | undefined = undefined;
    private fqcnCache: NodeName | undefined = undefined;
    private technicalFQCNCache: NodeName | undefined = undefined;
    private typeCache: NodeType | undefined = undefined;
    private technicalNameCache: NodeName | undefined = undefined;

    public getId(): NodeId {
        return this.id;
    }

    public getName(): NodeName {
        return this.name;
    }

    public getTechnicalName(): NodeName {
        if(!this.technicalNameCache) {
            this.technicalNameCache = this.getParsedMetadata().$nodeName || this.getName();
        }

        return this.technicalNameCache as NodeName;
    }

    public getFQCN(defaultService: string): NodeName {
        if(!this.fqcnCache) {
            this.fqcnCache = fqcn(this, false, false, true, defaultService);
        }
        return this.fqcnCache;
    }

    public getTechnicalFQCN(defaultService: string): NodeName {
        if(!this.technicalFQCNCache) {
            this.technicalFQCNCache = fqcn(this, false, false, false, defaultService);
        }
        return this.technicalFQCNCache;
    }

    public changeName(name: NodeName): NodeRecord {
        return this.set('name', name);
    }

    public getDescription(): NodeDescription {
        return this.description;
    }

    public getType(): NodeType {
        if(!this.typeCache) {
            this.typeCache = this.getParsedMetadata().$nodeType || this.type;
        }

        return this.typeCache as NodeType;
    }

    public getLink(): NodeLink | null {
        return this.link;
    }

    public getTags(): List<NodeTag> {
        return this.tags;
    }

    public isLayer(): boolean {
        return this.layer;
    }

    public isDefaultLayer(): boolean {
        return this.defaultLayer;
    }

    public getParent(): Node | null {
        return this.parent;
    }

    public changeParent(parent: Node | null): Node {
        return this.set('parent', parent);
    }

    public children(): List<Node> {
        return this.childrenList;
    }

    public getSources(): List<Node> {
        return this.sourcesList;
    }

    public getTargets(): List<Node> {
        return this.targetsList;
    }

    public getGeometry(): GraphPoint {
        return this.geometry;
    }

    public getSize(): Size {
        return this.size;
    }

    public getMetadata(): string | null {
        return this.metadata;
    }

    public getParsedMetadata(): {[metaKey: string]: any} {
        const parsed = this.get('parsedMetadata');

        if(!parsed) {
            if(!this.parsedMetadataCache) {
                this.parsedMetadataCache = parseNodeMetadata(this);
            }

            return this.parsedMetadataCache;
        }

        return parsed;
    }


    public setChildren(children: List<Node>): Node {
        return this.set('childrenList', children);
    }

    public changeMetadata(metadata: string): NodeRecord {
        const copy = this.set('metadata', metadata);
        return copy.set('parsedMetadata', Map(parseNodeMetadata(copy)));
    }

    public isEnabled(): boolean {
        return this.get('enabled');
    }

    public changeGeometry(geometry: GraphPoint): Node {
        return this.set('geometry', geometry);
    }

    public changeSize(size: Size): Node {
        return this.set('size', size);
    }

    public getVersion(): number {
        return this.get('version', 1) as number;
    }
}
