import {ServiceName} from "../../../model/Board";
import {Node, NodeType} from "../../../model/Graph";
import {isAggregateEvent} from "../event/is-aggregate-event";
import {getVoMetadata} from "../vo/get-vo-metadata";
import {detectService} from "./detect-service";
import {names} from "./names";

export const fqcn = (node: Node, forQuery?: boolean, forViewModel?: boolean, useLabel?: boolean, defaultService?: string) => {
  let service = detectService(node);

  const nodeName = useLabel ? node.getName() : node.getTechnicalName();

  if (service) {
    service += '.';
  } else {
    service = '';
  }

  if(node.getType() === NodeType.document) {
    if(forQuery) {
      return `${service}Get${names(nodeName).className}`;
    }

    if(forViewModel) {
      return `${service}${names(nodeName).className}`;
    }

    const voMeta = getVoMetadata(node);

    if(voMeta.ns) {
      const ns = voMeta.ns.charAt(0) === "/" ? voMeta.ns.slice(1) : voMeta.ns;
      return `${service}${ns.split("/").map(n => names(n).className).join(".")}.${names(nodeName).className}`;
    }
  }

  if(node.getType() === NodeType.event) {
    if(isAggregateEvent(node)) {
      const aggregate = node.getSources().first(undefined);

      if(aggregate && aggregate.getType() === NodeType.aggregate) {
        return `${service}${names(aggregate.getTechnicalName()).className}.${names(nodeName).className}`;
      }
    }
  }

  return `${service}${names(nodeName).className}`;
}
